.login {
  background-image: url("https://metro.co.uk/wp-content/uploads/2020/08/1-2-2897.jpg?quality=90&strip=all");
  /* background-repeat: no-repeat; */
  max-width: 100%;
  max-height: 100%;
}

.login {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* .login__background {
  width: 100%;
  height: 100%;
} */

.login__logo {
  margin-top: 20px;
  margin-bottom: 20px;
  object-fit: contain;
  width: 100px;
  margin-right: auto;
  margin-left: auto;
  z-index: 1;
}

.login__container {
  width: 300px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0);
  opacity: 0.9;
  padding: 40px;
}

.login__container > h1 {
  font-weight: 500;
  margin-bottom: 20px;
  color: rgb(255, 255, 255);
}

.login__container > form > h5 {
  margin-bottom: 5px;
  color: rgb(255, 255, 255);
}

.login__container > form > input {
  height: 30px;
  margin-bottom: 10px;
  background-color: rgba(255, 255, 255, 0.4);
  border: 2px solid;
  border-color: rgba(0, 0, 0, 0.363);
  width: 98%;
}

.login__container > p {
  margin-top: 15px;
  font-size: 12px;
  color: aliceblue;
  font-weight: lighter;
}

.login__signInButton {
  background: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
}

.login__registerButton {
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: darkgray;
}
