.Footer {
    height: 100px;
    width: 100%;
    display: flex;
    align-items: center; 
    background-color: rgb(59, 4, 43);
    position: relative;
    bottom: 0;
    z-index: 100;
    justify-content: space-evenly;
  }

  .Footer__FacebookIcon {
    display: flex;
    justify-content: center;
    color: white;
    opacity: 0.6;
    margin-left: 70px;
    margin-bottom: 10px;
  }

  .Footer__Facebook {
    display: flex;
    flex-direction: column;
    color: white;
    opacity: 0.6;
    font-weight: bolder;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }

  .Footer__InstagramIcon {
