.Jam {
    background-color: aquamarine;
}

.Jammy__background {
    padding: 10px;
    margin: 20px 0;
    border: 1px solid lightgray;
    background-color: rgb(46, 1, 40);
    position: relative;
}


.Jammy__greeting {
    /* display: flex; */
    padding: 20px;
    /* margin: 20px 0; */
    background-color: rgba(41, 5, 58, 0.137);
    align-items: center;
    color: azure;
}

.Jammy__greeting > h2 {
    text-align: center;
}


.Jammy__instruction {
    padding: 40px;
    margin: 20px 0;
    border: 1px solid rgb(11, 0, 14);
    background-color:  rgba(40, 70, 88, 0.137);
    position: relative;
}

.Jammy__instruction > h3 {
    text-align: center;
    font-weight: bolder;
    font-size: 30px;
}

.Jammy__instruction > p {
    text-align: left;
    font-weight: lighter;
    font-family: cursive;
    padding: 5px;
}

.Jammy__payment > p {
    text-align: left;
    font-weight: lighter;
    font-family: cursive;
    padding: 5px; 
}